<template>
    <div class='chart-container'>
        <canvas :id='chartID' class='gauge' style='height: 100%; width: 100%;' height='auto' width='auto'></canvas>
    </div>
</template>
<script>
import Chart from 'chart.js';
export default {
    computed:{
        getChartLabels:function(){
            return this.buildData.labels;
        },

        getChartData:function(){
            return this.buildData.data;
        }
    },
    
    
    props:['buildData','chartID','chartColors'],

    data(){
        return{
            chart:'',
        }
    },

    /*computed:{
        updateChart:function(){
            let chartData = this.chartValues
            return chartData 
        },

        scalePresent:function(){
            if(this.chartType === 'bar'){
                return { yAxes: [{ ticks: { beginAtZero: true } }] }  
            }
        }
    },*/

    watch:{
        updateChart: function(){
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[0].data = this.updateChart
            this.chart.update()
        }
    },

    mounted: function(){
    const ctx = document.getElementById(this.chartID);
    this.chart = new Chart(ctx,{
      type: 'pie',
      data:{
        labels: this.getChartLabels,
        datasets:[{
          backgroundColor:this.chartColors,
          data:this.getChartData,
        }]
      },
      options:{
          
            legend:{
                display: this.displayLegend
            },
            animation:{
                animateRotate: true
            },
            defaultFontFamily:'Monospace',
            defaultFontStyle: 'normal',
            defaultFontColor:'#fff', 
            responsive: true,
            maintainAspectRatio: false,
            drawborder: false,            
            scales: this.scalePresent,        
        },
        centerText:{
            display:true,
            text:'TESTING',
        },
    })  
  }
}
</script>

<style>    
    .chart-container{
        width:100%;
        height:100%;
        position:relative;
    }

    canvas{
        position:absolute;
        width:100%;
        height:100%;
    }
</style>
