<template>
    <div class='chart-container'>
        <canvas :id='chartID' class='gauge' style='height: 100%; width: 100%;' height='auto' width='auto'></canvas>
    </div>
</template>
<script>
import Chart from 'chart.js';
export default {
    props:['chartID','chartBuild','chartColors'],

    data(){
        return{
            chart:'',
        }
    },

    computed:{
        updateChart:function(){
            let chartData = this.chartBuild.data;
            return chartData 
        },

        scalePresent:function(){
            if(this.chartType === 'bar'){
                return { yAxes: [{ ticks: { beginAtZero: true } }] }  
            }
        }
    },

    watch:{
        updateChart: function(){
            this.chart.data.labels = [];
            this.chart.data.labels = this.chartBuild.labels;
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[0].data = this.updateChart
            this.chart.update()
        }
    },

    mounted: function(){
    defaultFontColor:'rgb(255, 255, 255)'; 
    
    const ctx = document.getElementById(this.chartID);
    this.chart = new Chart(ctx,{
      type: 'pie',
      data:{
        labels: this.chartLabels,
        datasets:[{
          backgroundColor:this.chartColors,
          data:this.chartValues,
        }]
      },
      options:{
            elements:{
                arc:{
                    borderWidth: 0
                }
            },
    
            legend:{
                display: true,
                fontColor: 'rgb(255, 255, 255)',
      
                position:'right',
                labels:{
                    fontColor:'white',
                    fontFamily:'roboto',
                    fontSize: 8,
                    boxWidth: 4,
                    usePointStyle: true,

                }
                
            },
            animation:{                
                animateScale: false,
                animateRotate: false,
            },
            defaultFontFamily:'Monospace',
            defaultFontStyle: 'normal',
            defaultFontColor:'rgb(255, 255, 255)', 
            responsive: true,
            maintainAspectRatio: false,
            drawborder: false,            
            scales: this.scalePresent,        
        },
    })  
  }
}
</script>

<style>    
    canvas{
        position: absolute;
        height: auto;
        width: 100%;
        margin: auto auto;
    }
</style>
