<template>
    <div class='form__content'>
        <div class='title'>GREENHOUSE</div>
        <div class='section'>
            <input-select :fieldLabel="greenhouseLabel" :options="greenhouseOptions" v-model='greenhouse' v-on:input='setGreenhouse()'/>
            <div class='line__item--flex' v-for='index in 3' :key='index'>
                <input-select :fieldLabel="plantSpeciesLabel" :options="plantSpeciesOptions" v-model='plantSpecies[index-1].species' v-on:input='setPlantSpecies(index-1)'/>
                <input-number :fieldLabel="plantAreaLabel" v-model='plantSpecies[index-1].area' v-on:input='setPlantSpecies(index-1)'/>
                <input-select :fieldLabel="plantSpeciesLabel" :options="plantSpeciesOptions" v-model='plantSpecies[index+2].species' v-on:input='setPlantSpecies(index+2)'/>
                <input-number :fieldLabel="plantAreaLabel" v-model='plantSpecies[index+2].area' v-on:input='setPlantSpecies(index+2)'/>
            </div>
        </div>
    </div>
</template>

<script>
import InputSelect from '@/components/Input_Select';
import InputNumber from '@/components/Input_Number';
import {mapState,mapMutations,mapGetters} from 'vuex'
export default {   
    components:{
        'input-select':InputSelect,
        'input-number':InputNumber,
    },

    data(){
        return{
            greenhouse:'',
            plantSpecies:[
                {'species':'','area':0},
                {'species':'','area':0},
                {'species':'','area':0},
                {'species':'','area':0},
                {'species':'','area':0},
                {'species':'','area':0},
            ],

            greenhouseLabel:'GREENHOUSE',
            plantSpeciesLabel:'SPECIES',
            plantAreaLabel:'SQR METERS',
            
            greenhouseOptions:[
                {
                    'value':'none',
                    'key':'none',
                    'text':'None'
                },
                {
                    'value':'small',
                    'key':'small',
                    'text':'Small - Greenhouse'
                },
                {
                    'value':'medium',
                    'key':'medium',
                    'text':'Medium - Greenhouse'
                },
                {
                    'value':'large',
                    'key':'large',
                    'text':'Large - Greenhouse'
                },
            ],

            plantSpeciesOptions:[
                {
                    'value':'none',
                    'key':'none',
                    'text':'None',
                },
                {
                    'value':'rice',
                    'key':'rice',
                    'text':'Rice',
                },
                {
                    'value':'cabbage',
                    'key':'cabbage',
                    'text':'Cabbage',
                },
                {
                    'value':'lettuce',
                    'key':'lettuce',
                    'text':'Lettuce',
                },
            ]
        }
    },

    methods:{
        ...mapMutations([
            'SETGREENHOUSE','SETPLANTSPECIES',
        ]),
        setGreenhouse:function(){
            this.SETGREENHOUSE(this.greenhouse);
        },
        setPlantSpecies:function(index){
            let species = {
                'index':index,
                'species':this.plantSpecies[index].species,
                'area':this.plantSpecies[index].area
            }

            if(species.area == null || species.area === ''){
                species.area = 0;
            }

            console.log(species.area);
            if(species.species != null && species.species !== '' && species.species !== 'none'){
                this.SETPLANTSPECIES(species);
            }
        }
    },

    computed: {
        ...mapGetters({
            remainingSpace:'calcGreenhouseRemaining',
        })
    }
}
</script>

<style lang="scss" scoped>
    @import '../scss/_config_form';
        
    .form__content{
        @extend %form__content;

        .title{
            @extend %form__section__title;
            
        }

        .section{
            @extend %form__section;
        }

        .line__item--flex{
            @extend %form__line__item--flex;
        }
    }
</style>
