<template>
    <div class='chart-container'>
        <canvas :id='chartID' class='gauge' style='height: 100%; width: 100%;' height='auto' width='auto'></canvas>
    </div>
</template>
<script>
import Chart from 'chart.js';
export default {
    props:['chartTitle','displayValue', 'chartID','chartLabels','chartValues','chartType','chartColors','displayLegend'],

    data(){
        return{
            chart:'',
        }
    },

    computed:{
        updateChart:function(){
            let chartData = this.chartValues
            return chartData 
        },

        scalePresent:function(){
            if(this.chartType === 'bar'){
                return { yAxes: [{ ticks: { beginAtZero: true } }] }  
            }
        }
    },

    watch:{
        updateChart: function(){
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[0].data = this.updateChart
            this.chart.update()
        }
    },

    mounted: function(){
    const ctx = document.getElementById(this.chartID);
    this.chart = new Chart(ctx,{
      type: 'bar',
      data:{
        labels: ['test','test2','test3','test4'],
        datasets: [
        {
            label: 'Low',
            data: [10,67.8,47,],
            backgroundColor: '#D6E9C6' // green
        },
        {
            label: 'Moderate',
            data: [,20.7],
            backgroundColor: '#FAEBCC' // yellow
        },
        {
            label: 'High',
            data: [,11.4,,4],
            backgroundColor: '#EBCCD1' // red
        }
        ]
      },
      options:{
            legend:{
                display: this.displayLegend
            },
            animation:{
                duration: .1
            },
            defaultFontFamily:'Monospace',
            defaultFontStyle: 'normal',
            defaultFontColor:'#fff', 
            responsive: true,
            maintainAspectRatio: false,
            drawborder: false,            
            scales: this.scalePresent,
            scales: {
            xAxes: [{ stacked: true }],
            yAxes: [{ stacked: true }]
        }        
        },
        centerText:{
            display:true,
            text:'TESTING',
        },

    })  
  }
}
</script>

<style>    
    .chart-container{
        width:100%;
        height:100%;
        position:relative;
    }

    canvas{
        position:absolute;
        width:100%;
        height:100%;
    }
</style>
