<template>
    <div class='helper__greenhouse'>                
         <chart-pie :chartID ="'1'" :chartColors="['#fff','#00aaee','#9C14BA','#37E12F','#33ff33','#C31CA0','#DED414']" :chartBuild="greenhouseChartData"/>
    </div>   
</template>

<script>
import {mapState,mapMutations,mapGetters} from 'vuex'
import ChartPie from '@/components/Chart-Pie'
export default {

    components:{
        'chart-pie':ChartPie,
    },

    computed:{
        ...mapGetters({
            greenhouseRemainingSpace:'calcGreenhouseRemaining',
            greenhouseAvailableSpace:'greenhouseAvailable',
            plantSpecies:'getPlantSpecies',
        }),

        greenhouseChartData:function(){
            let chartData = {
                'labels':[],
                'data':[]
            }

            chartData.labels.push('FREE SPACE');
            chartData.data.push(this.greenhouseRemainingSpace);

            let plants = this.plantSpecies;

            plants.forEach(element =>{
                if(element.species.length > 0 && element.species != 'none'){
                    chartData.labels.push(element.species.toUpperCase());
                    chartData.data.push(element.area);
                    
                }
            })
            console.log(chartData);
            return chartData;
        },
        
        
        greenhouseUsedSpace: function(){
            let remainingSpace = this.greenhouseRemainingSpace;
            let availableSpace = this.greenhouseAvailableSpace;

            return availableSpace - remainingSpace;
        },




        greenhouseLabels: function(){
            let labels = ['Available Space'];
            
            this.plantSpecies.forEach(element =>{
                if(element.species != null && element.species != '' && element.species != 'none')
                    labels.push(element.species);
            })
            console.log(labels);
            return labels;
        },

        greenhouseData:function(){
            let data = new Array();
            data.push(this.greenhouseRemainingSpace);

            this.plantSpecies.forEach(element =>{
                if(element.species != null && element.species != '' && element.species != 'none')
                    data.push(element.area);
            })
            console.log(data);
            return data;
        }
    }
}
</script>

